import Vue from 'vue'
import Router from 'vue-router'
import Articel from "../components/Articel";
import PostList from "../components/postList";
import userInfo from "../components/userInfo"

Vue.use(Router)

export default new Router({
  routes: [
    {
      name:'root',//router-link‰∏≠‰ΩøÁî® :to='root'Ë∑≥ËΩ¨
      path:"/",
      components:{ main: PostList},//mainÂ∞±ÊòØÂú®router-view‰∏≠‰ΩøÁî® name='main' ÂÆûÁé∞‰ΩÜÈ°µÈù¢ÂàáÊç¢
    },
    {
      name:'post_content',
      path:"/topic/:id",
      components:{ main: Articel},
    },
    {
      name:'user_info',
      path:"/userinfo/:name",
      components:{ main:userInfo }
    }
  ]
})



